<template>
	<div>
			

	<div class="row mx-auto " >
			<div class="col-md-12 p-0">
				<div class="mx-auto bg-dark">
					<div class="pt-3">
						<img class="mx-auto d-block w-100 register-img " src="../../static/img/gk05.jpg" alt="">
		<form @submit.prevent='onSubmit' class="container pb-3">
		<div class="form-group mt-3">
		<label>username</label>
		
		<input v-model="username" class="form-control " type="text">
		<small class="form-text text-muted">input your name
		</small>
		</div>

		<div class="form-group mt-3">
		<label>password</label>
		<input v-model="password" class="form-control" type="password">
		<small class="form-text text-muted">input your password</small>
		<div><input type="checkbox" v-model="cookieState">   <span class="text-muted ml-2">记住我</span></div>
		</div>
	

		<button class="btn btn-primary btn-block p-2 mb-2" type="submit">登录</button>
	</form>
					</div>

				</div>
			</div>
		</div>
	</div>
</template>	

<script>	
import axios from 'axios'
export default{
data(){
	return{
		username:'',
		password:'',
		cookieState:false,
	}
},
mounted(){
	this.username=this.getCookie('username')
	this.password=this.getCookie('password')
},
methods:{
	onSubmit(){
		axios.get('/user.json').then(res=>{
			const data=res.data;
			const users=[]; 
			for(let key in data){
			var user=data[key];
			 users.push(user);

			}

           var result=users.filter(user=>{
           	return user.username===this.username&&user.password===this.password;
           })
           if(result!=null&&result.length>0)
           {

           	this.$store.commit('setUser',result[0].username)
           	this.$router.push({name:'home'})
           }else{
           	alert('账户错误')
           	this.$store.commit('setUser','')
           	this.$store.commit('isLogin',false)

           }
		})
		if(this.cookieState&&this.username&&this.password)
		{
		  this.setCookie('username',this.username)
		  this.setCookie('password',this.password)

		}
	},
	 setCookie(name,value){          //名字  内容  过期时间
    var oDate=new Date();
    oDate.setDate(oDate.getDate()+7);
    document.cookie=name+'='+value+';expires'+'='+oDate;
},
 getCookie(name) {
    var arr=document.cookie.split('; ');   //注意是;加空格
    for(var i=0; i<arr.length; i++)
    {
        var arr2=arr[i].split('=');
        if(arr2[0]==name)
        {
            return arr2[1];
        }        
    }
    return "";
}
}


}
</script>

<style scoped>	
.register-img{height:30rem;}
label{color:white;}
</style>


