import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)
/*配置路由*/
import home from '../components/Home'
import menu from '../components/menu'
import login from '../components/login'
import about from '../components/about/about'
import register from '../components/register'
import manage from '../components/manage'

//about页面的二级路由

import Contact from '../components/about/Contact'
import Delivery from '../components/about/Delivery'
import History from '../components/about/History'
import Orderguide from '../components/about/Orderguide'

//contact下的三级路由
import Phone from '../components/about/contact/Phone'
import ContactName from '../components/about/contact/ContactName'

import {store} from '../store/store.js'

export default new Router({
	/*mode:"history",*/
  routes: [
  {path:'/',name:'home',components:{
    default:home,
    'deliveryLink':Delivery,
    'historyLink':History,
    'orderguideLink':Orderguide
  }},
  {path:'/menu',name:'menu',component:menu},
  {path:'/manage',name:'manage',component:manage},
  {path:'/about',name:'about',component:about,children:[

//about页面的二级路由
{path:"/about/Contact",name:'contactLink',component:Contact,children:[

//about下的contact下的三级路由
{path:"/about/phone", name:"PhoneLink", component:Phone},
{path:"/about/contactname", name:"ContactNameLink", component:ContactName},
]},


{path:"/about/Delivery",name:'deliveryLink',component:Delivery},
{path:"/about/History",name:'historyLink',component:History},
{path:"/about/Orderguide",name:'orderguideLink',component:Orderguide},
]},

{path:'/login',name:'login',component:login},
{path:'/register',name:'register',component:register},
{path:"*",redirect:"/"},
]
}
);




