<template>

  <nav class="navbar-nav navbar-expand fixed-top fontstyle navbar-light bg-light "  >

  <div class="navbar-nav navbar-expand" v-show="clientState">

    <ul class="navbar-nav nav-meg">
     <router-link :to="{name:'home'}" class="nav-link p-0 ">  <li class="logo"></li></router-link>
     <li ><router-link :to="{name:'home'}" class="nav-link px-4">主页</router-link></li>
     <li ><router-link :to="{name:'menu'}" class="nav-link px-4">菜单</router-link></li>
     <li ><router-link :to="{name:'manage'}" class="nav-link px-4">管理</router-link></li>
     <li ><router-link :to="{name:'about'}" class="nav-link px-4">关于我们</router-link></li>
   </ul>

</div>

  <ul class="navbar-nav ml-auto nav-meg">
    <li><router-link tag="a" :to="{name:'login'}"  class="nav-link" v-show="!isLogin">登录</router-link></li>

    <li class="nav-link text-warning user">
      {{currentUser}}
    </li>

    <li class="exit" v-show="isLogin" @click='exitUser'><router-link :to="{name:'home'}" class="nav-link"   >[退出]</router-link></li>

    <li><router-link tag="a" :to="{name:'register'}" v-show="!isLogin" class="nav-link">注册</router-link></li>
  </ul>
 






  <div class="fixed-menu" v-show="!clientState" >
    <div class=" showLeftMenuBtn" @click.stop="toggleMenuContent"><p>|||</p></div>
      <!-- 移动端菜单按钮 -->
    <div  v-show="!clientState">
    <div ref="menu" class="left-menu" v-show="showLeftMenu">
      <ul class="nav flex-column" >
        <router-link :to="{name:'home'}" ><li>主页</li></router-link>
        <router-link :to="{name:'menu'}" ><li>菜单</li></router-link>
        <router-link :to="{name:'manage'}" ><li>管理</li></router-link>
        <router-link :to="{name:'about'}" ><li>关于</li></router-link>
      </ul>
    </div>
    </div>
  </div>

</nav>

</template>

<script> 
import {store} from '../store/store.js'

export default {
  name: 'Header',
  data(){
    return{
      showLeftMenu: false,
    }
  },
      mounted() {
        document.addEventListener('click', this.handleDocumentClick);
        document.addEventListener('touchstart', this.handleDocumentClick);
    },
  methods:{
    exitUser(){
      this.$store.commit('exitUser',false)
    },
    toggleMenuContent () {
      this.showLeftMenu = !this.showLeftMenu
    },
      handleDocumentClick(e) {
                if (!this.$refs.menu.contains(e.target)) {
                    this.showLeftMenu = false;
                } 
              }

  },
  computed:{
    isLogin(){ return this.$store.getters.isLogin},
    currentUser(){ return this.$store.getters.currentUser},
    clientState(){
      return this.$store.getters.setClientState;
    }
  },



}
</script>

<style scoped>

.nav-header{
  width:100%;
  height:40px;
  background: #666666;
  margin:0;
  padding:0;
}
ul{margin: 0; padding:0;}
p{margin:0;}
.nav-meg li :hover{background: #ccc}

.logo{
  width:80px;
  height: 100%;
  background: url('../../static/img/logo.jpg'); 
  background-size:100% 100%;
}

.fontstyle{font-family:Trebuchet MS;font-weight: 100;}
.user {cursor:pointer}
.exit :hover{ color:red; }         
.fixed-menu{
  position:absolute;
  left:0;
  top:0;
  width:100px;
  height:auto;
  line-height:40px;
  text-align:center;
  
}
.left-menu{
  width:100px;
  height:auto;
  background: #666666;
  border-radius: 8px;
}
.left-menu a{
  color:black !important;
  text-decoration: none;
}
.left-menu ul li{
  list-style: none;

}
.left-menu ul li:hover{
  background: #ccc;

}
.showLeftMenuBtn{
  width:50px;
  height:40px;
  background: #ccc;
  cursor:pointer;

}
</style>


