<template>
	<div>
	<div class="row mx-auto" >
			<div class="col-md-12 p-0">
				<div class="mx-auto bg-dark">
					<div class="pt-3">
						<img class="mx-auto d-block w-100 register-img " src="../../static/img/gk02.jpg" alt="">
		<form class="container pb-3" @submit.prevent="onSubmit">
		<div class="form-group mt-3">
		<label>username</label>
		
		<input v-model="username" class="form-control text-primary " type="text">
		<small class="form-text text-muted">input your name
		</small>
		</div>

		<div class="form-group mt-3">
		<label>password</label>
		<input v-model="password" class="form-control" type="password">
		<small class="form-text text-muted">input your password</small>
		</div>
		<div class="form-group mt-3">
		<label>comfirmPassword</label>
		<input  type="password" v-model="confirmPassword" class="form-control">
		<small class="form-text text-muted">input your password again</small>
		</div>

		<button class="btn btn-primary btn-block  pb-2 p-2" type="submit">注册</button>
	</form>
					</div>

				</div>
			</div>
			</div>
	</div>
</template>	

<script>	
import axios from 'axios'

export default{
data(){
	return{
		username:'',
		password:'',
		confirmPassword:'',
	}
},
methods:{
	onSubmit(){
		if(this.password===this.confirmPassword&this.password!=''&this.username!='')
			{
				const userData={
					username:this.username,
					password:this.password
				};
				axios.post('/user.json',userData)
				.then(res=>{console.log(res);alert('注册成功');this.$router.push({name:'login'})})
				;

			}else{
				if(this.password===''|| this.username)
				{
					alert('请输入注册信息')
				}else{
				alert('两次密码不一致')
				}
			}
	}

}


}
</script>

<style scoped>	
.register-img{height:30rem;}
label{color:white;}
</style>


