<template>
	<div class="background">
		<div class="row mx-auto">

			<div class="col-md-8 col-sm-12">
				<manageItem v-on:$updata="updata"></manageItem>
			</div>
			<div class="col-md-4 col-sm-12">
				<h1 class="py-3 text-secondary">菜单</h1>

				<table class="table table-hover">
					<thead>
						<th>商品</th>
						<th>删除</th>
					</thead> 
					<tbody v-for="item in manageItems" :key="item.id">
						<tr>
							<td>{{item.name}}</td>
							<td><button class="btn btn-sm btn-outline-dange" @click="deleteItem(item)" >x</button></td>
						</tr>
					</tbody>
				</table>
			</div>

		</div>

	</div>

</template>
<script>
import manageItem from './manageItem.vue'

export default{	


	components:{manageItem},
	data(){
		return{
		}
	},
	created(){this.updata()},
	/*created:function(){this.updata()},*/
	methods:{
		updata(){
			fetch("https://wd7671337397bxyqyc.wilddogio.com/goods.json"
				).then(res=>{
					return res.json();
				})
				.then(data=>{
					var menuArray=[];

					for(let key in data){
						data[key].id=key;
						menuArray.push(data[key])
					}			
					this.$store.commit('setMenuItems',menuArray);

				})},



				deleteItem(item){
					fetch("https://wd7671337397bxyqyc.wilddogio.com/goods/"+item.id+"/.json",{
						method:"DELETE",
						headers:{
							'Content-type':'application/json',						
						}
					});
					this.$store.commit('deleteItem',item)				
				}

			},
			computed:{
				manageItems:{
        // 在vuex中获取数据
        get(){
        	//通过getters获取数据
	return	this.$store.getters.getMenuItems
       
      },
      set(){

      }
  }
},
}


</script>
<style scoped>
.background{ background: url(../../static/img/gk11.jpg);
	background-size:100% 100%; 
	min-height:93vh;
}
</style>


