 <template>
 	<div>
 		<div class="row mx-auto background">
 			<div class=" col-sm-12 col-md-8 mt-3">	
 				<table class="table    table-border  border-bottom border-primary tbackground" >	
 					<thead class="thead-primary">	
 						<tr>	
 							<th>明星</th>
 							<th>大小</th>
 							<th>价格</th>
 							<th></th>
 						</tr>
 					</thead>
 					<tbody>	
 						<tr v-for="(item,index1) in MenuItems" >
 							<td>{{item.name}}<br><strong>{{item.description}}</strong></td>
 							<td> 
 								<tr class="d-block pointer"   :class="{active:isActive[index1]==index2}" 
 								v-for="(option,index2) in item.options" :key="option.size" @click='chooseSize(index1,index2,option.size)'>{{option.size}}</tr>
 							</td>
 							<td> 
 								<tr class="d-block"  v-for="option in item.options" :key="option.price">￥{{option.price}}</tr>
 							</td>

 							<td  class="align-middle">
 								<button class="btn btn-success "  @click="choose(item,item.options,index1)">+</button>
 							</td>
 						</tr>	
 					</tbody>


 				</table>
 			</div>

 			<!-- 购物车 -->
 			<div class="col-sm-12 col-md-4 mt-3 " v-if="baskets.length>0">
 				<table class="table tbackground table-border  border-bottom border-primary  m-0 ">
 					<thead>
 						<th>商品</th>
 						<th>数量</th>
 						<th>价格</th>
 						<th></th>

 					</thead>
 					<tbody v-for="item in baskets" >
 						<tr>

 							<td>
 								{{item.name}}  ({{item.size}})
 							</td>
 							<td>
 								<button class="btn btn-sm bg-white" @click="reduceQuantity(item) ">-</button>
 								<span>{{item.quantity}}</span>
 								<button class="btn btn-sm bg-white" @click="addQuantity(item)" >+</button>
 							</td>
 							<td>{{item.price * item.quantity}}</td>
 							<td><button class="btn btn-sm btn-secondary rounded" @click="deleteItem(item)">x</button></td>
 						</tr>

 					</tbody>
 				</table>
 				<div class="tbackground">
 					<br>
 					<p>总价：<span class="price">{{total}}</span></p>
 					<button class="btn btn-success btn-block">下单</button>
 				</div>
 			</div>
 			<div v-else class="text-dark h-25 bg-secondary mt-3">{{basketsText}}</div>

 		</div>
 	</div>
 </template>	
 <script>
 import Vue from 'vue';
 export default{
 	data(){
 		return{

 			baskets:[],
 			basketsText:"购物车还没添加任何东西~",
 			lala:'',
 		}	
 	},
 	created(){this.updata();},


 	methods:{
 		updata(){
			//将请求的数据放在vuex中
			fetch("https://wd7671337397bxyqyc.wilddogio.com/goods.json"
				).then(res=>{
					return res.json();
				})
				.then(data=>{
					this.$store.commit('setMenuItems',data);
				})},

				setBasket(item,options,index1){
					let basket={
						"name":item.name,
						"size":options[this.isActive[index1]].size,
						"price":options[this.isActive[index1]].price,
						"quantity":1,
					};
					if(this.baskets.length>0){
						let result=this.baskets.filter(data=>{
							return (data.name===item.name&&data.size===options[this.isActive[index1]].size)
						});

						if(result.length>0)
						{
							result[0].quantity+=1;
						}else{
							this.baskets.push(basket)
						}


					}else{

						this.baskets.push(basket)
					}
				},

				choose(item,options,index1){

					if(this.isActive[index1]==-1){

						if(!options[1])
						{
							this.isActive[index1]=0;
							this.setBasket(item,options,index1)
						}else{
							return ;
						}
					}else{
						this.setBasket(item,options,index1)			
					}
				},

				chooseSize(index1,index2){
					Vue.set(this.isActive, index1, index2)
					this.isActive={};
					console.log(this.isActive)

				},


				reduceQuantity(item){
					if(item.quantity>1)
						item.quantity-=1;
				},

				addQuantity(item){
					item.quantity+=1;
				},
				deleteItem(item){
					this.baskets.splice(this.baskets.indexOf(item),1)
				}

			},
			computed:{
				total(){
					var totalCount=0;
					for(var i in this.baskets)
					{
						var goods=this.baskets[i];
						totalCount+=goods.price*goods.quantity;
					} 
					return totalCount;
				},
				 pause(){
      return this.$store.getters.getPause
      },

				MenuItems(){
		//在vue中获取数据
		/*return this.$store.state.menuItems*/
		//通过getters获取数据
		return this.$store.getters.getMenuItems
	},
	
	isActive:{
		get(){
			var isActiveCount={};
			for(let key in this.MenuItems)
			{
				isActiveCount[key]=-1;
			}

			this.lala=isActiveCount;
			return isActiveCount;
		}, 	
		set(val){
		},	

	},



}

}



</script>	

<style scoped>
.background{ background: url(../../static/img/gk13.jpg);
	background-size:100% 100%; 
	min-height:93vh;
}
.tbackground{
	background:#ccc;
	opacity: .8;
}
.price{
	font-weight: 700;
	font-size:2rem;
	color:red;}
	.active{background:red;}
	.pointer{cursor:pointer;}
	</style>
}




