<template>
	<div>
		<form class="pb-2">
			<div class="form-group">
				<h1 class="py-3 text-secondary">添加新的商品</h1>

				<label class="text-secondary">商品</label>
				<input type="text" class="form-control" v-model="newSet.name" >
				
			</div>
			<label class="text-secondary">描述</label>
			<textarea class="form-control" rows="4"  v-model="newSet.description"></textarea>

			<div  class="p-3 text-secondary"><label>添加选项 </label><div  class="btn btn-outline-primary btn-sm ml-3"  @click="addType">+</div>
		</div>
		<div class="form-group text-secondary" v-for="(item,index) in types" >
			<label><strong>类型{{item}}</strong></label><br>
			<label class="text-primary">尺寸</label>
			<input type="text" class="form-control" v-model="newSet['size'+index]" >
			<label class="text-primary">价格</label>
			<input type="text" class="form-control"  v-model="newSet['price'+index]">

		</div>
		<button  type="button" @click="submit" class="btn btn-success btn-block">提交</button>

	</form>

</div>
</template>
<script>
export default{
	data(){
		return{
			types:[1],
			newSet:[]
		}
	},
	methods:{
		$updata(){
			this.$emit('$updata');
		},
		
		addType(){
			this.types.push(this.types.length+1)
		},
		submit(){
			let options=[];
			for(var i=0; i<10 ;i++)
			{	
				var size=this.newSet['size'+i]
				var price=this.newSet['price'+i]
				if(size&&price)
				{
					let option={'size':size,'price':price};
					options.push(option);
				} 
				else{break}; 
			};
			let data={
				name:this.newSet.name,
				description:this.newSet.description,
				options:options,
			}

			fetch('https://wd7671337397bxyqyc.wilddogio.com/goods.json',{
				method:"POST",
				headers:{"content-type":'application/json'},
				body:JSON.stringify(data),
			})
				/*.then(data=>{
					console.log(data)
				})*/
				.then(()=>{
					this.$updata()	

				})
				.catch(err=>{
					console.log(err)
				})

			},
			
		}
	}
	</script>
	<style scoped>
		input,textarea{background: #ccc}
		  input:focus{background: #ccc}
		  textarea:focus{background: #ccc}

	</style>


