<template>
  <div id="app" @resize="isApp">
    <navHeader></navHeader>
    <div>
    <router-view></router-view>
     </div>
     
  </div>
</template>

<script>
import navHeader from './components/Header.vue'
export default {
  name: 'App',
  mounted(){
    this.isApp();
     window.onresize = () => {
      this.isApp()
    }
  },
  methods:{
    isApp () {
      let isTrue = false
      if (document.body.clientWidth < 768) {
        isTrue = false
      } else {
        isTrue = true
      }
      this.$store.commit('setClientState',isTrue)
    },

  },
  components:{
    navHeader,
  }
}
</script>

<style>
body{margin:0;padding-top: 40px}
</style>



